import { Directive, Input, Output, EventEmitter, AfterViewInit, Injector } from '@angular/core';
import { LookupComponent } from '@farris/ui-lookup';
import { of } from 'rxjs';

@Directive({
  selector: '[lookup-extra]'
})
export class LookupDirective implements AfterViewInit {
  @Input('lookup-extra')
  public code: string;
  @Input('ins') lookup: LookupComponent;
  @Output() onSelectRows: EventEmitter<any> = new EventEmitter<any>();
  constructor() {
  }
  ngAfterViewInit(): void {
    if (this.lookup) {
      this.lookup.dictPicked = (rows: any) => {
        if (this.onSelectRows) {
          this.onSelectRows.emit({ code: this.code, rows: [].concat(rows) })
        }
        const result = {
          closeDialog: true
        };
        return of(result);
      };
    }
  }
}